        </section>

    </div>
</template>

<script>
import cHeader from "../components/cHeader.vue";
import { prices } from "../config/prices";
export default {
    metaInfo() {
        return {
            title: 'Program Ishrane',
            titleTemplate: '%s | ' + 'Softvare za nutricioniste',
        }
    },
    data() {
        return {
            prices,
            customers: [
                { img: "mfuub.jpg", title: "Medicinski fakultet univerzitet u Beogradu" },
                { img: "ptfo.png", title: "Prehrambeno tehnoloski fakultet Osijek" },
                { img: "vup.png", title: "Veleuilite u Poegi" },
                { img: "zavod_za_javno_zdravstvo_dubrovacko_neretvanske_zupanije.png", title: "Zavod za javno zdravstvo Dubrovacko Neretvanske upanije" },
                { img: "opca_bolnica_zadar.png", title: "Opa bolnica Zadar" },
                { img: "zavod_za_javno_zdravstvo_medimurske_zupanije.jpg", title: "Zavod za javno zdravstvo Medimurske upanije" },
                { img: "univerzitet_modernih_znanosti_mostar.jpg", title: "Univerzitet modernih znanosti Mostar" },
                { img: "zzjzvz.png", title: "Zavod za janov zravstvo Varadinske upanije" },
                { img: "sbzmr_varazdinske_toplice.jpg", title: "Specijalna bolnica za rehabilitaciju Varadinske Toplice" },
                { img: "zzjzv.png", title: "Zavod za javno zdravlje Valjeno" },
                { img: "vssszovits.png", title: "Visoka kola strukovnih studija za obrazovanje vaspitaa i trenera u Subotici" },
                { img: "sveucilisna_klinicka_bolnica_mostar.png", title: "Sveuilina klinika bolnica mostar" },
                { img: "hzjz.png", title: "Hrvatski zavod za javno zdravstvo" },
                { img: "institut_za_zdravlje_i_sigurnost_hrane_zenica.jpg", title: "Institut za zdravlje i sigurnost hrane Zenica" },
                { img: "taekwondo_klub_novi_grad_sarajevo.png", title: "Taekwondo klub Novi Grad Sarajevo" },
                { img: "pb_rab.png", title: "Psihijatrijska bolnica Rab" },
                { img: "zavod_za_javno_zdravstvo_pgz.jpg", title: "Zavod za javno zdravstvo Primorsko goranske upanije" },
                { img: "zavod_za_javno_zdravlje_subotica.jpg", title: "Zavod za javno zdravlje Subotica" }
            ],
        };
    },
    components: {
        cHeader,
    },
    methods: {
        convertAndFormat(value) {
            const rsd = value * prices.conversionRate;
            return new Intl.NumberFormat('sr-RS', {
                style: 'currency',
                currency: prices.currency,
                minimumFractionDigits: 2
            }).format(rsd);
        }
    }
};
</script>


